<template>
  <div className="container" style="min-height: 100%; padding-bottom: 100px;">
    <el-container>
      <el-header align="center">实验背景</el-header>
      <el-main
      >
        甲苯氧化是典型的芳烃空气氧化反应，氧化产物含有苯甲醛、苯甲醇与苯甲酸。传统钴锰溴催化甲苯空气氧化体系反应的主要氧化产物为苯甲酸，伴随苯甲酸需求市场的日益饱和以及苯甲酸制备苯酚的高成本，选择性甲苯空气氧化制备高附加值的苯甲醛与苯甲醇受到科学研究者日益重视。尤为重要的是采用甲苯选择性空气氧化制备的苯甲醛与苯甲醇，可以解决目前通过氯化水解方法生产苯甲醛苯甲醇所带来环境不友好和产品含氯素等问题。因而甲苯选择性空气氧化被认为是一种制备不含氯素的苯甲醛与苯甲醇的绿色工艺。
        酶是一类生物催化剂，具有降低反应活化能、不改变反应的方向和平衡关系等的一般催化剂共性，同时酶作为生物催化剂具有高催化活性、高度的专一性、反应条件温和等优点。采用酶作为选择性催化氧化有机化合物成为当今科研工作者研究的热点。为了利用酶的优点，避免其易变和失活对环境要求苛刻的缺点，科学工作者研究用仿酶催化剂来模拟生物酶的催化性能。在过去的
        20 多年，采用金属卟啉模拟细胞色素 P-450
        单加氧酶催化碳氢化合物氧化取得了长足的进步。人们对细胞色素 P-450
        单加氧酶模拟体系首先研究是以 H2O2、ROOH、PhIO、NaOCl、KHSO5
        等化学计量氧为氧源的金属卟啉选择性催化反应体系开始的
        。但是由于化学计量氧的氧化性强，催化剂金属卟啉容易失活，近年来
        利用清洁分子氧为氧源的金属卟啉选择 性催化反应越来越受到人们的重视
        。尤其是分子氧直接氧化体系即 Lyons
        体系，由于其高的催化活性，无需还原剂等绿色催化氧化条件，分子氧廉价易得等特性，使得该体系更接近工业应用。然而上述对金属卟啉仿生催化甲苯的研究主要是从分子角度对甲苯仿生催化氧化本征反应的认识，实际上甲苯仿生催化氧化过程涉及到气液传质，反应操作方式，反应器操作特征等工程因素，这些因素对甲苯仿生催化氧化选择性有很大的影响。因此根据甲苯仿生催化和化学催化耦合过程机理建立反应动力学，定量认识甲苯仿生催化和化学催化耦合过程关系，定量解释实验现象提供可靠的依据，及反应器的选择是研究甲苯选择性氧化的重要课题。
        此外学习如何控制甲苯选择性氧化有助于我们对工业中择性氧化类反应的相关知识有更深刻认识，对学习选择环己烷等烃类化合物选择性氧化具有同样重要的参考价值。
      </el-main
      >
    </el-container>
  </div>
</template>

